import React from 'react';
import style from '../User/User.module.sass'
import DetectableOverflow from 'react-detectable-overflow';
import {useState} from "react";


let User = (props) => {
    let [isOverflowName, setIsOverflowName] = useState(false)
    let [isOverflowEmail, setIsOverflowEmail] = useState(false)

    let handleChangeName = (isOverflowed) => {
        setIsOverflowName(isOverflowed)
    }
    let handleChangeEmail = (isOverflowed) => {
        setIsOverflowEmail(isOverflowed)
    }

    let styleOverflow = {
        width: '200px',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        margin: '0 auto'
    }

    return(<div className={style.user__container}>
            <div>
                <img src={props.user.photo}/>
            </div>
            <div className={style.name__item}>
                <div className={style.tooltip__container}>
                    <DetectableOverflow onChange={handleChangeName} style={styleOverflow}>
                        {props.user.name}
                    </DetectableOverflow>
                    {isOverflowName && <div className={style.tooltip}>{props.user.name}</div>}
                </div>
            </div>
            <div className={style.position__item}>
                {props.user.position}
            </div>
            <div className={style.email__item}>
                <div className={style.tooltip__container}>
                    <DetectableOverflow onChange={handleChangeEmail} style={styleOverflow}>
                        {props.user.email}
                    </DetectableOverflow>
                    {isOverflowEmail && <div className={style.tooltip}>{props.user.email}</div>}
                </div>
            </div>
            <div className={style.phone__item}>
                {props.user.phone}
            </div>
    </div>);
}

export default User;