import React, {useState} from 'react'
import logophoto from '../../Image/logo.svg'
import menuIcon from '../../Image/menu icon.svg'
import style from './Header.module.sass'
import {NavLink} from "react-router-dom";
import {useMediaQuery} from 'react-responsive'
import MenuSlider from "../Popup/MenuSlider";

let Header = (props) => {
    const isDesktopOrLaptop = useMediaQuery({
        query: '(max-device-width: 768px)'          //width when must use burgerMenu
    })

    let [isMenu, setIsMenu] = useState(false);      //hook for active/deactivate menu

    let onSuccess = () => {
        setIsMenu(!isMenu);
    }

    let focusToReg = () => {
        document.getElementById("registration").focus();
    }

    return (
        <div className={style.header__container}>
            <div className={style.header__item}>
                <img src={logophoto}/>
                {isDesktopOrLaptop ? <div>
                    <div className={style.menu__item} onClick={onSuccess}><img src={menuIcon}/></div>
                    {isMenu ? <MenuSlider onSuccess={onSuccess}/> : ""}</div> : <div>
                    <div onClick={focusToReg}><NavLink to={"1"} activeClassName={style.active__item}>Sign Up</NavLink>
                    </div>
                    <div onClick={focusToReg}><NavLink to={"2"} activeClassName={style.active__item}>Users</NavLink>
                    </div>
                    <div onClick={focusToReg}><NavLink to={"3"}
                                                       activeClassName={style.active__item}>Requirements</NavLink></div>
                    <div onClick={focusToReg}><NavLink to={"4"}
                                                       activeClassName={style.active__item}>Relationships</NavLink>
                    </div>
                    <div onClick={focusToReg}><NavLink to={"5"} activeClassName={style.active__item}>About me</NavLink>
                    </div>
                </div>}
            </div>
        </div>);

}

export default Header;